import {translate as t} from '../../utils.mjs';

import PaginatedList from '../base/PaginatedList.jsx';
import ImagedListItem from '../base/ImagedListItem.jsx';



/** Represents paginated list of all users 
 * 
 * 	Props:
 * 		@param {boolean} isLoading		true if the parent component is in 'loading' state.
 * 		@param {boolean} isError		true if the parent component is failed to reload this item list.
 * 						WHEN THE PARENT COMPONENT IS LOADING OR HAVE ERROR STATE, the navigation between
 * 						pages is not possible
 * 		@param {EntityPage}	items 		the entity page to show
 * 		@param {callback} onItemSelect		The function calls when the user clicks on a single item in the list
 * 
 * 	State:
 * 		@param {Array of Entity} items 	full list of all items containing in all downloaded entity pages.
 * 										Items from pages that are not downloaded yet were not shown.
 * 		@param {boolean} isLoading		true the following page is still loading, false otherwise
 * 		@param {Error|null} error 		The error object if the component is in the error state/
 */
export default class UserList extends PaginatedList{

	/** Renders content where single item will be shown
	 * 	@param {User} user the user to be rendered
	 */
	renderItemContent(user){
		/* user.surname or user.name are not required fields, so they may equal to null */
		let userHeader = (`${user.surname || ''} ${user.name || ''}`).trim();
		if (userHeader === ''){
			userHeader = user.login;
		}

		return (<ImagedListItem img={user.avatar}
			imageWidth={75} imageHeight={75} item={user} onClick={this.props.onItemSelect}>
				<h2>{userHeader}</h2>
				<small>{t('Login')}: {user.login}</small>
		</ImagedListItem>);
	}

	render(){
		if ((this.items === null || this.items.length === 0)
			&& !this.props.isLoading && !this.props.isError){
			return <p><i>{t("There are no users satisfying search criteria.")}</i></p>
		} else {
			return super.render()
		}
	}

}
